module.exports = "/* Style inputs, select elements and textareas */\ninput,\nselect,\ntextarea {\n  width: 60%;\n  padding: 15px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  box-sizing: border-box;\n  resize: vertical;\n  float: left;\n  margin-left: 22em;\n}\n/* Style the label to display next to the inputs */\nlabel {\n  font-family: fantasy;\n  padding: 11px 53px 13px 0;\n  display: inline-block;\n}\n/* Style the submit button */\nbutton {\n  background-color: #4caf50;\n  color: white;\n  padding: 12px 20px;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  float: left;\n  margin: 3em 32em 2em 34em;\n}\n/* Style the container */\n.form-container {\n  width: 100%;\n  text-align: center;\n  background-color: #ffffff;\n  margin-top: 4rem;\n}\nform {\n  display: inline-block;\n  border-radius: 5px;\n  margin: 0 auto;\n  padding: 20px;\n  border: 2px solid #a6a6a6;\n  background-color: #f2f2f2;\n}\n.invalid-feedback {\n  color: #ff0000;\n  font-family: monospace;\n}\n/* Floating column for labels: 25% width */\n.col-25 {\n  float: left;\n  width: 25%;\n  margin-top: 6px;\n}\n/* Floating column for inputs: 75% width */\n.col-75 {\n  float: left;\n  width: 75%;\n  margin-top: 6px;\n  margin-bottom: 8px;\n}\n/* Clear floats after the columns */\n.row:after {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */\n@media screen and (max-width: 600px) {\n  .col-25,\n.col-75,\ninput[type=submit] {\n    width: 100%;\n    margin-top: 0;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"